@import '../../sass/styles.scss';

.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    color:$font-color;
    height:14vh;
    overflow: hidden;
    transition: height 1s ease-in-out;
    @media only screen and (max-width: 980px) {
        // height:80vh;
        margin: 0rem 2rem;
        flex-direction: column;
    }
    .toggle-collapse {
        position: absolute;
        top: 0%;
        width: 95%;
        cursor: pointer;
        .toggle-icon {
            display: flex;
            justify-content: flex-end;
            padding: 2rem 0;
            font-size: 1.4rem;
            button {
                background-color: transparent;
                border: none;
                font-size: 1.4rem;
            }
        }
    }
    .nav-brand {
        margin: 0.7rem 4rem;
        h1 {
            font-weight: lighter;
        }
        @media only screen and (max-width: 480px) {
            margin: .4rem .4rem;
        }
    }
    .nav-menu {
        ul {
            margin: 1rem 4rem;
            @media only screen and (max-width: 480px) {
                margin-left: 0rem;
                padding-left: 1rem;
            }
        }
        li {
            display: inline-block;
            padding: 3.5vh 2rem;
            list-style-type: none;
            a {
                text-decoration: none;
                color:$font-color;
            }
            @media only screen and (max-width: 1080px) {
                padding: 3.5vh 1rem;
            }
            @media only screen and (max-width: 980px) {
                display: flex;
                flex-direction: column;
                padding: 4vh 2rem;
            }
        }
    }
    .nav-blank-menu {
        display: none;
        @media only screen and (max-width: 980px) {
            display: initial;
            color: white;
        }
    }
}
.collapse {
    @media only screen and (max-width: 980px) {
        height:90vh;
    }
    @media only screen and (max-width: 480px) {
        height:100vh;
    }
}